import React, { Component } from "react";
import { Link } from "react-router-dom";

import "./navBar.style.css";

class NavBar extends Component {
    render() {
        return (
            <div>
                <div className="">
                    <nav className="navBar">
                        <div className="left-nav">
                            <div className="nav__item">
                                <Link className="linkTag" to="/">Newsy</Link>
                            </div>

                            <div className="nav__item">
                                <Link className="linkTag" to="/">Top Headlines</Link>
                            </div>
                        </div>

                        <div className="right-nav">
                            <div className="nav__item">
                                <Link className="linkTag" to="/Sports">Sports</Link>
                            </div>
                            <div className="nav__item">
                                <Link className="linkTag" to="/science">Science</Link>
                            </div>
                            <div className="nav__item">
                                <Link className="linkTag" to="/technology">Technology</Link>
                            </div>
                            <div className="nav__item">
                                <Link className="linkTag" to="/entertainment">Entertainment</Link>
                            </div>
                        </div>

                    </nav>
                </div>
            </div>
        );
    }
}

export default NavBar;
