import "./App.css";
import NavBar from "./components/navBar/navBar.component";
import NewsContainer from "./components/newsContainer/newsContainer.component";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <Router>
        <NavBar />
        {/* <NewsContainer category="general" /> */}

        <Switch>
          <Route exact path="/">
            <NewsContainer key="general" category="general" />
          </Route>
          <Route exact path="/Sports">
            <NewsContainer key="sports" category="sports" />
          </Route>

          <Route exact path="/Technology">
            <NewsContainer key="technology" category="technology" />
          </Route>

          <Route exact path="/Science">
            <NewsContainer key="science" category="science" />
          </Route>

          <Route exact path="/Entertainment">
            <NewsContainer key="entertainment" category="entertainment" />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}
// 048b39e82bc04c2dbdb0e87d7785e71f
export default App;
