import React, { Component } from 'react'
import "./newsItem.style.css";


class NewsItem extends Component {
    render() {

        let { imageUrl, description, title, newsUrl } = this.props;

        return (
            <div className="newsItems" >
                <div className="card">
                    <img src={imageUrl} />
                    <div className="container">
                        <h4>{title}</h4>
                        <p>{description}</p>
                    </div>
                    <a href={newsUrl} target="blank" className="cardBtn" >See More</a>
                </div>
            </div>
        )
    }
}

export default NewsItem;