import React, { Component } from "react";
import NewsItem from "../newsItems/newsItem.component";
import InfiniteScroll from "react-infinite-scroll-component";

import "./newsComponent.style.css";

class NewsContainer extends Component {
    constructor () {
        super();
        this.state = {
            article: [],
            totalResult: 0,
            page: 1
        };
    }

    async componentDidMount() {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=048b39e82bc04c2dbdb0e87d7785e71f&page=${this.state.page}&pagesize=9`;
        let fetchData = await fetch(url);
        let data = await fetchData.json();
        console.log(data);

        this.setState({
            article: data.articles,
            page: this.state.page + 1,
            totalResult: data.totalResults,
        });
    }

    fetchMoreData = async () => {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=048b39e82bc04c2dbdb0e87d7785e71f&page=${this.state.page}&pagesize=9`;

        let fetchData = await fetch(url);
        let data = await fetchData.json();

        this.setState({
            article: this.state.article.concat(data.articles),
            page: this.state.page + 1,
            totalResult: this.state.totalResult + data.totalResults,
        });
    };

    render() {
        return (
            <div>
                <InfiniteScroll
                    dataLength={this.state.article.length}
                    next={this.fetchMoreData}
                    hasMore={this.state.article.length !== this.state.totalResult}
                >

                </InfiniteScroll>

                <div className="newsContainer">
                    {this.state.article.map((element) => {
                        return (
                            <NewsItem
                                newsUrl={element.url ? element.url : ""}
                                imageUrl={element.urlToImage}
                                title={element.title ? element.title : ""}
                                description={
                                    element.description ? element.description : ""
                                }
                            />
                        );
                    })}
                </div>
            </div>
        );
    }
}

export default NewsContainer;
